ul.items {
    display: flex;
    flex-wrap: wrap;
    li {
        display: flex;
        align-content: center;
        min-height: 300px;
        border: 1px solid $gray;
        // border-radius: $border-radius;
        position: relative;
        margin: 1rem;
        a:hover:after {
            content: '+';
            font-size: 3rem;
            position: absolute;
            top:35px;
            right:30px;
            color: $turquoise;
            text-shadow: 2px 2px 15px $turquoise;
        }
    }
}

.catalog ul.items li {
    p {
        margin-left: 0;
        margin-right: 0;
        width: auto;
    }
    img {
        width: 190px;
    }
    a {
        display: block;
        background: $item-bg;
        padding: 1rem;
        // border-radius: $border-radius;
        text-decoration: none;
        &:hover {
            opacity: 1;
            color: #666;
        }
    }
}

.media-details form {
    margin-left: 0;
}

tr {
    text-align: left;
}

td, th {
    padding: 5px 5px;
}

form {
    width: 475px;
    margin: 34px auto;
    th {
        width: 150px;
        vertical-align: middle;
        padding: 8px;
    }
    td {
        padding: 15px 15px;
    }
    tr {
        text-align:left;
        vertical-align: top;
        padding:2px;
    }
    table {
        width: 475px;
        margin-bottom: 16px;
    }
}

form td {
    form td select,
    form td input,
    form td textarea {
        width: 100%;
        border-radius: 4px;
        padding: 10px;
        border: 1px solid #a5a5a5;
        font-size: 14px;
        font-family: 'Open Sans', sans-serif;
    }
}
form input[type="submit"] {
    width: 475px;
    text-align: center;
    border: 0;
    background: #3888c2;
    color: #FFF;
    -webkit-border-radius: 4px;
    border-radius: 4px;
    font-size: 16px;
    padding: 14px 0 16px;
    font-family: 'Open Sans', sans-serif;
}
form input[type="submit"]:hover {
    background: #358dce;
    cursor: pointer;
}

.search {
    background: #f0eeed;
    border-bottom: 3px solid #dddddd;
    width:100%;
    text-align: right;
    form {
        margin: 5px 5px 5px auto;
        input[type="submit"] {
            width: auto;
            text-align: center;
            border: 0;
            background: #3888c2;
            color: #FFF;
            border-radius: 4px;
            font-size: 16px;
            padding: 6px;
            font-family: 'Open Sans', sans-serif;
        }
    }
}

.page p.message {
    background: #ffeca4;
    border: 1px solid #f16702;
    padding: 1em;
    width: 444px;
}
.note-designer {
    font-size: 14px;
    font-style: italic;
    font-weight: bold;
}

#content {
    min-height: 400px;
    background: $light-black;
    flex: 1 0 auto;
}
